import { useRef } from 'react';
function UrlEncoder() {
  const encodeTextRef = useRef();
  const decodeTextRef = useRef();

  const onEncode = async (event) => {
    event.preventDefault();
    const inputUrl = encodeTextRef.current.value;
    if (!inputUrl) return;
    const response = await fetch(`https://amansaw.com/encoder/encode/${inputUrl}`);
    decodeTextRef.current.innerHTML = await response.text();
  };

  const onDecode = async (event) => {
    event.preventDefault();
    const inputUrl = encodeTextRef.current.value;
    if (!inputUrl) return;
    const response = await fetch(`https://amansaw.com/encoder/decode/${inputUrl}`);
    decodeTextRef.current.value = await response.text();
  };

  return (
    <div className="UrlEncoder">
      <ul className="nav nav-pills mt-3 flex-row justify-content-around" id="pills-tab" role="tablist">
        <li className="nav-item col-4" role="presentation">
          <button className="nav-link active" id="pills-home-tab" data-bs-toggle="pill" data-bs-target="#pills-encode" type="button" role="tab" aria-controls="pills-home" aria-selected="true">Url Encoder</button>
        </li>
        <li className="nav-item col-4" role="presentation">
          <button className="nav-link" id="pills-profile-tab" data-bs-toggle="pill" data-bs-target="#pills-decode" type="button" role="tab" aria-controls="pills-profile" aria-selected="false">Url Decoder</button>
        </li>
      </ul>

      <form>
        <div className="my-5">
          <label htmlFor="input-url" className="form-label"> Url Input </label>
          <textarea ref={encodeTextRef} className="form-control" id="url-input" rows={8}></textarea>
        </div>

        <div className="tab-content row mx-5" id="pills-tabContent">
          <button type="submit" onClick={onEncode} className="btn btn-primary tab-pane fade show active" role="tabpanel" id="pills-encode"> Encode </button>
          <button type="submit" onClick={onDecode} className="btn btn-success tab-pane fade" role="tabpanel" id="pills-decode"> Decode </button>
        </div>

        <div className="my-5">
          <label htmlFor="input-output" className="form-label"> Url Output </label>
          <textarea ref={decodeTextRef} className="form-control" disabled id="url-output" rows={8}></textarea>
        </div>
      </form>
    </div>
  )
}

export default UrlEncoder;
