import { useEffect, useState } from "react";
import md from "../assets/blog1.md";
import Markdown from "./Markdown";
import "./Blogs.css";

function Blogs() {
  const [rawMd, setRawMd] = useState("");
  useEffect(() => {
    fetch(md)
      .then((res) => res.text())
      .then((text) => {
        setRawMd(text);
        console.log(text);
      });
  }, []);

  return <Markdown mdtext={rawMd} />;
}

export default Blogs;
